.header{
  border: 2px solid red;
    height: 75px;
    display: flex;
    background-color: #e91e63;
    color: white;
}
  .logo{
    width: 131px;
    background-color: white;
  }
  
  .menus{
    width: 89%;
    display: flex;
      justify-content: space-evenly;
      align-items: center;
      color:white;
  }
  .link{
    color:white;
    text-decoration: none;
    font-size: 22px;
    font-weight: bolder;
  }
.logo-img{
  width: 108px;
  margin-left: 2px;
  margin-top: 20px;
}
.lable{
  display: inline-block;
  width: 18%;
}
.space{
margin-bottom: 10px;
}

.customers {
  font-family: Arial, Helvetica, sans-serif;
  border-collapse: collapse;
  width: 100%;
}

.customers td, .customers th {
  border: 1px solid #ddd;
  padding: 8px;
}

.customers tr:nth-child(even){background-color: #f2f2f2;}

.customers tr:hover {background-color: #ddd;}

.customers th {
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: left;
  background-color: #04AA6D;
  color: white;
}

.image{
  border-radius: 70px;
  width: 125px;
  height: 125px;
  border: dotted;
}